.testimonials {
  background-color: $clr1;
  height: 745px;
  width: 100%;
  @include for-mobile-layout {
    height: 1410px;
  }
}

.testimonials-title {
  margin-top: 140px;
  text-align: center;
  align-self: center;
  margin-bottom: 63px;

  @include for-mobile-layout {
    height: 124px;
    margin-bottom: 52px;
  }
}

.text-emphasis {
  color: #79c8c7;
}

.cards-container {
  @include for-mobile-layout {
    flex-wrap: wrap;
  }
}

.card-container:not(:last-child) {
  margin-right: 30px;
  @include for-mobile-layout {
    margin-right: 0;
  }
}

.card-container {
  max-width: 350px;

  @include for-mobile-layout {
    margin-bottom: 48px;
  }
}

.card-subcontainer {
  position: relative;
  top: -21px;
}

.quotes-img-container {
  width: 67px;
  height: 56px;
}

.card-text {
  width: 100%;
  height: 97px;
  font-family: "livvic-semi";
  font-size: rem(15px);
  line-height: 25px;
  text-align: center;
  margin-bottom: 27px;

  @include for-mobile-layout {
    margin-bottom: 19px;
  }
}

.card-person-name {
  width: 100%;
  height: 28px;
  color: $clr3;
  font-family: "livvic-bold";
  font-size: 18px;
  line-height: 28px;
  text-align: center;
  margin-bottom: 2px;
}

.card-person-job-title {
  width: 100%;
  height: 18px;
  font-family: "livvic-med-ita";
  font-size: rem(13px);
  line-height: 18px;
  text-align: center;
  margin-bottom: 32px;

  @include for-mobile-layout {
    margin-bottom: 16px;
  }
}

.card-person-img-container {
  height: 62px;
  width: 62px;
}
