.footer {
  background-color: $clr2;
  height: 196px;

  @include for-mobile-layout {
    height: 432px;
    font-size: rem(15px);
  }
}

.footer-subcontainer {
  @include for-mobile-layout {
    flex-direction: column;
    align-items: center;
  }
}

.footer-menu {
  height: 100%;
  width: 160px;
  margin-right: 125px;

  @include for-mobile-layout {
    margin-right: 0;
    align-items: center;
  }
}

.footer-logo-container {
  height: 40px;
  margin-top: 48px;
  margin-bottom: 25px;

  @include for-mobile-layout {
    margin-top: 66px;
    margin-bottom: 24px;
    height: 24px;

    img {
      height: 100%;
    }
  }
}

.footer-menu-buttons div:first-child {
  margin-right: 43px;

  @include for-mobile-layout {
    margin-right: 27px;
  }
}

.address-container {
  margin-top: 48px;
  width: 445px;
  height: 94px;
  opacity: 0.6;
  font-size: rem(15px);
  line-height: 25px;

  @include for-mobile-layout {
    width: 100%;
    margin-top: 24px;
    text-align: center;
  }
}
