.main-container {
  // max-width: $normal-breakpoint;
  margin: 0 auto;
  padding: 0 10px;

  @include for-mobile-layout {
    max-width: $mobile-breakpoint;
    padding: 0 24px;
  }
  @include for-normal-layout {
    max-width: $narrow-breakpoint;
  }
  @include for-wide-layout {
    max-width: $normal-breakpoint;
  }
}

.title {
  font-family: "livvic-bold";
  font-size: rem(48px);
  line-height: 48px;

  @include for-mobile-layout {
    font-size: rem(30px);
    line-height: 32px;
  }
}
