* {
    box-sizing: border-box;
}

html {
    line-height: 1.6;
}

body {
    font-family: 'livvic-semi', sans-serif;
    font-size: rem(18px);
    color: $clr5;
    margin: 0;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'livvic-semi', sans-serif;
    margin-top: 0;
    font-weight: normal;
}

p {
    margin: 0;
    padding: 0;
}