.call-to-action {
  height: 200px;
  background-color: $clr4;
  color: $clr2;

  @include for-mobile-layout {
    height: 302px;
  }
}

.call-to-action-container {
  height: 100%;
  width: 100%;
  align-items: center;
  @include for-mobile-layout {
    flex-direction: column;
    align-items: center;
  }
}

.call-to-action-title {
  width: 504px;
  height: 51px;
  margin-left: 98px;
  margin-right: 237px;

  @include for-mobile-layout {
    width: 100%;
    margin-top: 83px;
    margin-bottom: 54px;
    margin-left: 0;
    margin-right: 0;
    text-align: center;
  }
}
